import React from 'react';
import Table from '../../../Table/Table';
import { formatDate } from '../../../../utils';

function DocumentsInfoTable({ documents, onRowClick, onDelete }) {
  const columns = [
    { title: '', format: date => formatDate(date) },
    { title: '' },
    { title: '' },
    { title: '', styles: { style: { color: '#8AB65A' }, condition: value => value === ''}}
  ];

  const actions = [
    <button className={ 'secondary-button hidden' } onClick={ onDelete }>   </button>
  ];

  return (
    <Table 
      columns={ columns } 
      data={ documents } 
      onDelete= { onDelete }
      actions={ actions } 
      onRowClick={ onRowClick }
    />
  );
}

export default DocumentsInfoTable;